.app-classify {
  height: 100%;
  background: white;
  padding-bottom: 8px;
}
.app-classify .view-switch {
  margin: 14px 0 17px 0;
  text-align: center;
}
.app-classify .view-switch button {
  width: 97px;
  height: 28px;
  border-radius: 3px;
  font-size: 13px;
  padding: 0;
  text-align: center;
  box-shadow: 0 2px #278dbb;
}
.app-classify .view-switch .ant-btn-primary:focus,
.app-classify .view-switch .ant-btn-primary:hover {
  background-color: #2daee8;
  border-color: #2daee8;
}
.app-classify .classify {
  height: calc(100% - 87px);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.app-classify .classify > div {
  width: 150px;
  height: 30px;
  margin: 7px 0;
  border-radius: 15px;
  border: solid #d1d1d1 1px;
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
}
.app-classify .classify > div .icon {
  margin: 0 8px 0 14px;
}
.app-classify .classify > div .count {
  margin: 0 14px 0 auto;
}
.app-classify .classify .selected {
  background: #2db7f5;
  color: white;
  border: inherit;
}
.app-classify .doc {
  height: 28px;
  display: flex;
  justify-content: center;
}
.app-classify .doc button {
  border-radius: 14px;
  color: #00b0ff;
  border: 0;
  background: rgba(221, 221, 221, 0.46);
  font-size: 14px;
}
