.app-list {
    height: 100%;
    background: white;
    margin: 0 5px;
    
    .top {
        height: 44px;    
        display: flex;
        align-items: center;

        .button-group > button {
            width: 66px;
            height: 28px;
            border-radius: 3px;
            margin-left: 14px;
            color: #49a8e9;
            font-size: 13px;
            background: white;
            border: #49a8e9 1px solid;
            padding: 0 8px;
            span {
                margin-left: 6px;
            }
            &.disabled-btn {
                color: #ccc;
                background-color: #f7f7f7;
                border-color: #d9d9d9;
            }
        }

        .button-group > :nth-child(1) {
            margin-left: 24px;
        }

        .search-app {
            width: 230px;
            height: 28px;
            margin-left: auto;
            input {
                border-radius: 3px !important;
            }
        }

        .create-app > button {
            width: 85px;
            height: 28px;
            margin: 0 13px 0 20px;
            padding-left: 9px;
        }
    }
    .body {
        height: calc(~'100% - 44px');
        table {
            color: black;
            thead {
                font-size: 12px;
                font-weight: bold;
            }
            tbody {
                font-size: 13px;
                .app-name {
                    display: flex;
                    align-items: center;
                    font-weight: bold;
                }
            }
            img {
                width: 16px;
                height: 16px;
                border-radius: 3px;
                margin-right: 5px;
            }
        }
    }
}