.app-detail {
    height: 100%;
    background: white;

    .top {
        height: 44px;
        border-bottom: solid #e6e7eb 1px;
        font-size: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .name {
        height: 120px;
        border-bottom: solid #e6e7eb 1px;
        display: flex;
        align-items: center;
        .icon {
            width: 76px;
            height: 76px;
            margin-left: 20px;
            border-radius: 9.9px;
            box-shadow: 0 4px 4px 0 rgba(132, 132, 132, 0.5);
            img {
                width: 76px;
                height: 76px;
                border-radius: 9.9px;
            }
        }
        .title {
            height: 34px;
            padding: 7px 0 0 17px;
            font-size: 18px;
            font-weight: bold;
            color: black;
        }
        .btn-group {
            height: 42px;
            padding-left: 17px;
            button {
                font-size: 13px;
                -webkit-transform: scale(0.84,0.84);
                margin: 0 8px 0 -5px;
                width: 67px;
                height: 25px;
                color: white;
                padding: 0;
                border: 0;
            }
        }
    }

    .version {
        border-bottom: solid #e6e7eb 1px;
        .content {
            max-height: 72px;
            overflow-x: hidden;
            overflow-y: auto;
            .list {
                margin-left: 18px;
            }
        }
    }

    .description {
        border-bottom: solid #e6e7eb 1px;
        .content {
            max-height: 54px;
            overflow-x: hidden;
            overflow-y: auto;
        }
    }

    .dev-info {
        .content {
            overflow-x: hidden;
            overflow-y: auto;
            height: calc(~'100% - 24px');;
            & > div {
                width: 343px;
                border-bottom: solid #e6e7eb 1px;
                padding: 8px 7px 8px 5px
            }
            .sub-title {
                font-size: 12px;
                font-weight: bold;
                color: black;
            }
            .permisson .sub-content, .audit-event .sub-content {
                .table-wrap {
                    border: 1px solid #8a8a8a;
                    border-radius: 5px;
                    width: 314px;
                    margin: 9px 0;
                    position: relative;

                    & > span {
                        position: absolute;
                        top: -1px;
                        right: 3px;
                        color: white;
                        font-weight: bold;
                    }

                    & > img {
                        position: absolute;
                        top: 0;
                        right: 0;
                        width: 25px;
                    }

                    table {
                        width: 100%;
                        height: 100%;
                        & > :nth-child(odd) {
                            background: #f2f2f2;
                        }
                        
                        tr {
                            height: 23px;
                            td {
                                padding-left: 15px;
                            }
                            & :nth-child(2) {
                                border-left: 1px solid #e1e1e1;
                                word-wrap: break-word;
                                word-break: break-all;
                                white-space: normal;
                            }

                            &:first-child {
                                td:first-child {
                                    border-top-left-radius: 5px;
                                }
                                td:last-child {
                                    border-top-right-radius: 5px;
                                }
                            }
        
                            &:last-child {
                                td:first-child {
                                    border-bottom-left-radius: 5px;
                                }
                                td:last-child {
                                    border-bottom-right-radius: 5px;
                                }
                            }

                        }

                    }

                }

            }

            .audit-event .sub-content {
                table > :first-child {
                    font-weight: bold;
                }
            }
        }
    }

    .version, .description, .dev-info {
        padding: 12px 32px 18px 4px;
        .title {
            font-size: 16px;
            font-weight: bold;
            color: black;
            span {
                color: #2e97de;
            }
        }
        .content {
            margin: 5px 0 0 34px;
        }
    }
}