/************************************************ macroscopic 滚动条 ***********************************************/
::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #ebecf1;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #2e97de;
}
.root {
  height: 100%;
}
.root > div {
  height: 100%;
}
.root > div > .ant-spin-container {
  height: 100%;
}
.root button {
  border-radius: 3px;
}
.ant-modal.ant-confirm.ant-confirm-confirm {
  top: 250px;
}
