.root-wrap {
    height: 100%;
    display: flex;
    padding: 2px 3px 3px 0;
    .app-classify{
        width: 162px;
    }
    .app-list{
        width: calc(~'100% - 570px');;
    }
    .app-detail{
        width: 408px;
    }
}

.root-wrap-modal {
    display: flex;
    align-items: center;
    justify-content: center;

    .ant-modal {
        top: 0;
        width: 1056px !important;
        height: 683px;

        .ant-modal-content {
            height: 100%;
            .ant-modal-close-x {
                color: #ff0000;
            }
            .ant-modal-body {
                padding: 0;
                height: 100%;

                .action > button {
                    width: 90px;
                    height: 28px;
                    border-radius: 3px;
                    background-color: #2e97de;
                    color: white;
                    border: white;
                }
            }

            .title {
                width: 237px;
                height: 50px;
                border-radius: 100px;
                background-color: #2e97de;
                position: absolute;
                top: -22px;
                left: 409px;
                border: 3px solid white;
                display: flex;
                justify-content: center;
                align-items: center;
                font-size: 22px;
                font-weight: 500;
                color: white;
            }
        }
    }
}