.error-401 {
    background: #3398af;
    h1 {
        height: 86px;
        text-align: center;
        line-height: 86px;
        color: white;
        font-size: 30px;    
    }

    .center {
        height: calc(~ '100% - 86px');
    }
    
    .agileits-content {
        height: 100%;
        float: left;
        width: 56%;
        background: url('../../../../img/left.png') center no-repeat;
        background-size: cover;
        h2 {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 9.5em;
            color: white;
        }
    }

    .w3layouts-right {
        float: left;
        width: 44%;
        background: url('../../../../img/right.png') center no-repeat;
        background-size: cover;
        height: 100%;
    }
    .w3layouts-right h3 {
        color: #3397af;
        font-size: 2.5em;
        margin-top: 9em;
        text-transform: capitalize;
    }
    .w3layouts-right h4 {
        text-transform: capitalize;
        margin: 1.5em 0 1em;
        color: #000;
        font-size: 1.2em;
    }

    .w3ls-text{
        padding-left:5em;
    }
    .w3ls-text a {
        color: #000;
        text-decoration: underline;
    }
    .w3ls-text a:hover {
        color: #fff;
    }
}