.app-detail {
  height: 100%;
  background: white;
}
.app-detail .top {
  height: 44px;
  border-bottom: solid #e6e7eb 1px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-detail .name {
  height: 120px;
  border-bottom: solid #e6e7eb 1px;
  display: flex;
  align-items: center;
}
.app-detail .name .icon {
  width: 76px;
  height: 76px;
  margin-left: 20px;
  border-radius: 9.9px;
  box-shadow: 0 4px 4px 0 rgba(132, 132, 132, 0.5);
}
.app-detail .name .icon img {
  width: 76px;
  height: 76px;
  border-radius: 9.9px;
}
.app-detail .name .title {
  height: 34px;
  padding: 7px 0 0 17px;
  font-size: 18px;
  font-weight: bold;
  color: black;
}
.app-detail .name .btn-group {
  height: 42px;
  padding-left: 17px;
}
.app-detail .name .btn-group button {
  font-size: 13px;
  -webkit-transform: scale(0.84, 0.84);
  margin: 0 8px 0 -5px;
  width: 67px;
  height: 25px;
  color: white;
  padding: 0;
  border: 0;
}
.app-detail .version {
  border-bottom: solid #e6e7eb 1px;
}
.app-detail .version .content {
  max-height: 72px;
  overflow-x: hidden;
  overflow-y: auto;
}
.app-detail .version .content .list {
  margin-left: 18px;
}
.app-detail .description {
  border-bottom: solid #e6e7eb 1px;
}
.app-detail .description .content {
  max-height: 54px;
  overflow-x: hidden;
  overflow-y: auto;
}
.app-detail .dev-info .content {
  overflow-x: hidden;
  overflow-y: auto;
  height: calc(100% - 24px);
}
.app-detail .dev-info .content > div {
  width: 343px;
  border-bottom: solid #e6e7eb 1px;
  padding: 8px 7px 8px 5px;
}
.app-detail .dev-info .content .sub-title {
  font-size: 12px;
  font-weight: bold;
  color: black;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap {
  border: 1px solid #8a8a8a;
  border-radius: 5px;
  width: 314px;
  margin: 9px 0;
  position: relative;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap > span,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap > span {
  position: absolute;
  top: -1px;
  right: 3px;
  color: white;
  font-weight: bold;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap > img,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap > img {
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table {
  width: 100%;
  height: 100%;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table > :nth-child(odd),
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table > :nth-child(odd) {
  background: #f2f2f2;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr {
  height: 23px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr td,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr td {
  padding-left: 15px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr :nth-child(2),
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr :nth-child(2) {
  border-left: 1px solid #e1e1e1;
  word-wrap: break-word;
  word-break: break-all;
  white-space: normal;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr:first-child td:first-child,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr:first-child td:first-child {
  border-top-left-radius: 5px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr:first-child td:last-child,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr:first-child td:last-child {
  border-top-right-radius: 5px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr:last-child td:first-child,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr:last-child td:first-child {
  border-bottom-left-radius: 5px;
}
.app-detail .dev-info .content .permisson .sub-content .table-wrap table tr:last-child td:last-child,
.app-detail .dev-info .content .audit-event .sub-content .table-wrap table tr:last-child td:last-child {
  border-bottom-right-radius: 5px;
}
.app-detail .dev-info .content .audit-event .sub-content table > :first-child {
  font-weight: bold;
}
.app-detail .version,
.app-detail .description,
.app-detail .dev-info {
  padding: 12px 32px 18px 4px;
}
.app-detail .version .title,
.app-detail .description .title,
.app-detail .dev-info .title {
  font-size: 16px;
  font-weight: bold;
  color: black;
}
.app-detail .version .title span,
.app-detail .description .title span,
.app-detail .dev-info .title span {
  color: #2e97de;
}
.app-detail .version .content,
.app-detail .description .content,
.app-detail .dev-info .content {
  margin: 5px 0 0 34px;
}
