.root-wrap {
  height: 100%;
  display: flex;
  padding: 2px 3px 3px 0;
}
.root-wrap .app-classify {
  width: 162px;
}
.root-wrap .app-list {
  width: calc(100% - 570px);
}
.root-wrap .app-detail {
  width: 408px;
}
.root-wrap-modal {
  display: flex;
  align-items: center;
  justify-content: center;
}
.root-wrap-modal .ant-modal {
  top: 0;
  width: 1056px !important;
  height: 683px;
}
.root-wrap-modal .ant-modal .ant-modal-content {
  height: 100%;
}
.root-wrap-modal .ant-modal .ant-modal-content .ant-modal-close-x {
  color: #ff0000;
}
.root-wrap-modal .ant-modal .ant-modal-content .ant-modal-body {
  padding: 0;
  height: 100%;
}
.root-wrap-modal .ant-modal .ant-modal-content .ant-modal-body .action > button {
  width: 90px;
  height: 28px;
  border-radius: 3px;
  background-color: #2e97de;
  color: white;
  border: white;
}
.root-wrap-modal .ant-modal .ant-modal-content .title {
  width: 237px;
  height: 50px;
  border-radius: 100px;
  background-color: #2e97de;
  position: absolute;
  top: -22px;
  left: 409px;
  border: 3px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 22px;
  font-weight: 500;
  color: white;
}
