.create-app-wrap {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .step {
        width: 513px;
        margin: 40px 0 15px 0
    }

    .body {
        height: calc(~'100% - 83px');

        .base-info-wrap {
            width: 100%;
            height: 100%;

            .base-info {
                height: 100%;

                form {
                    width: 676px;
                    height: calc(~'100% - 68px');
                    border: #d2d2d2 1px solid;    
                    padding-top: 8px;

                    .ant-form-item {
                        margin-bottom: 7px;

                        & > :last-child {
                            position: static;

                            .ant-form-item-control {
                                position: static;
                                
                                .tip {
                                    position: absolute;
                                    color: rgb(204, 121, 121);
                                    margin-left: 10px;
                                }
                            }

                        }

                        input.ant-input-lg, .ant-select-lg .ant-select-selection--single {
                            height: 26px;

                            .ant-select-selection-selected-value {
                                height: 26px;
                                line-height: 26px;
                            }
                        }

                        .ant-form-item-control > div > button.btn.btn-default {
                            height: 26px;
                            display: flex;
                            align-items: center;
                        }
                    }

                    .ant-form-item-with-help {
                        margin-bottom: 0;
                    }

                    .ant-form-explain {
                        line-height: 1;
                        margin-top: 1px;
                        margin-bottom: 2px;
                    }

                    & > :last-child > .ant-col-17 {
                        .ant-form-item-control {
                            position: relative;
                            span.text-tip {
                                position: absolute;
                                bottom: 0;
                                right: 0;
                                line-height: 12px;
                                font-size: 12px;
                                padding: 0 5px 3px 0;
                            }
                        }
                        .has-error span.text-tip {
                            padding-bottom: 18px;
                        }

                        textarea {
                            word-break: break-all;
                        }
                    }
                }

                .action {
                    display: flex;
                    justify-content: space-around;
                    width: 676px;
                    margin: 20px 0;
                }
            }
            
        }

        .permission-info-wrap, .audit-info-wrap {
            width: 100%;
            height: 100%;

            .permission-info, .audit-info {
                height: 100%;

                .form {
                    width: 676px;
                    height: calc(~'100% - 68px');
                    border: #d2d2d2 1px solid;    
                    padding-top: 8px;
                    position: relative;

                    .up {
                        height: 280px;
                        border-bottom: #d2d2d2 1px solid;
                        padding: 50px 44px 0 44px;

                        .add-item {
                            padding-top: 20px;
                            text-align: center;

                            button {
                                border-radius: 3px;
                                background-color: #2AB292;
                                border-color: #2AB292;
                            }
                        }

                        .text-tip {
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            line-height: 12px;
                            font-size: 12px;
                            padding: 0 5px 3px 0;
                        }

                        .tip {
                            color: #cc7979;
                        }
                    }

                    .down {
                        height: calc(~ '100% - 280px');
                        padding: 27px 37px;

                        button {
                            color: #49a8e9;
                            font-size: 13px;
                            background: white;
                            border: #49a8e9 1px solid;
                            position: relative;
                            cursor: inherit;
                            margin: 0 30px 10px 0;
                            border-radius: 3px;
                            
                            .icon {
                                color: #ff3535;
                                position: absolute;
                                top: -7px;
                                right: -7px;
                                cursor: pointer;
                                font-size: 10px;
                                border: 2px white solid;
                            }

                            .disabled {
                                color: #ccc;
                                cursor: not-allowed;
                            }
                        }
                    }

                    .inner-action {
                        position: absolute;
                        right: 0;
                        bottom: 0;
                        padding: 0 12px 6px 0;
                        button { 
                            width: 52px;
                            height: 24px;
                            background: #f56c3b;
                            color: white;
                            padding: 0;
                            font-size: 13px;
                            border: 0;
                            border-radius: 3px;
                        }
                    }

                }


                .action {
                    display: flex;
                    justify-content: space-around;
                    width: 676px;
                    margin: 20px 0;
                }
            }

        }
    }
}

#audit-info {
    .add-card {
        height: 143px;
    }

    .text {
        height: 207px;
    }
}