.app-list {
  height: 100%;
  background: white;
  margin: 0 5px;
}
.app-list .top {
  height: 44px;
  display: flex;
  align-items: center;
}
.app-list .top .button-group > button {
  width: 66px;
  height: 28px;
  border-radius: 3px;
  margin-left: 14px;
  color: #49a8e9;
  font-size: 13px;
  background: white;
  border: #49a8e9 1px solid;
  padding: 0 8px;
}
.app-list .top .button-group > button span {
  margin-left: 6px;
}
.app-list .top .button-group > button.disabled-btn {
  color: #ccc;
  background-color: #f7f7f7;
  border-color: #d9d9d9;
}
.app-list .top .button-group > :nth-child(1) {
  margin-left: 24px;
}
.app-list .top .search-app {
  width: 230px;
  height: 28px;
  margin-left: auto;
}
.app-list .top .search-app input {
  border-radius: 3px !important;
}
.app-list .top .create-app > button {
  width: 85px;
  height: 28px;
  margin: 0 13px 0 20px;
  padding-left: 9px;
}
.app-list .body {
  height: calc(100% - 44px);
}
.app-list .body table {
  color: black;
}
.app-list .body table thead {
  font-size: 12px;
  font-weight: bold;
}
.app-list .body table tbody {
  font-size: 13px;
}
.app-list .body table tbody .app-name {
  display: flex;
  align-items: center;
  font-weight: bold;
}
.app-list .body table img {
  width: 16px;
  height: 16px;
  border-radius: 3px;
  margin-right: 5px;
}
